import { default as server} from '../server/server.js'

//获取房间列表
const getRoomList = (params) => {	
  return new Promise(function (resolve, reject) {
    server.getRoomList(params).then(function(data){   
      var data = data.data;   
      if(data.code === 'N0000'){
      console.log(data.data)
        resolve(data);
      }else{
        reject();
      }
    });
  });
}

export {
  getRoomList
}


