import axios from 'axios'
axios.defaults.timeout = 7000;

const getRoomList = (params) => {

	var storeyNo = params.storeyNo;
	var result;
	switch(storeyNo) {
		case 0:
			result = axios.get('http://localhost:8080/static/roomList0.json')
			break;
		case 1:	
			result = axios.get('http://localhost:8080/static/roomList1.json')		
			break;
		case 2:
			result = axios.get('http://localhost:8080/static/roomList2.json')
			break;	
		case 3:
			result = axios.get('http://localhost:8080/static/roomList3.json') 
			break;
		case 4:
			result = axios.get('http://localhost:8080/static/roomList4.json')
			break;
		default:
			break;		
	}
	return result;
	//请求房间列表API
	// return axios.get('apiurl', { params: params })
}


export default {
	getRoomList
}


